<template>
  <div>
      <slide-tab :tabList="tabList"></slide-tab>
      <ul class="pic-list">
      	<li v-for="(item,index) in picList">
      		<div class="pic">
      			<img :src="item.img_url" alt="">	 
      		</div>
      		<div class="tit">
      			{{item.title}}
      		</div>
			<router-link class="cover" :to="{name:'shareDetails',query:{picId:item.id}}"></router-link>	
      	</li>
      </ul>
  </div>
</template>
<script>
import eventVue from "@/assets/js/eventVue.js";
export default {
    data(){
        return{
            msg:"share",
            tabId:0,
            picList:[],
            tabList:[]
        }
    },
    created(){
		this.AxiosData(this.tabId);
		this.getTabId();
    this.AxiosTab();
    },
    watch:{
    	tabId(newV,oldV){
    		this.AxiosData(newV);	
    	}
    },
    methods:{
    	AxiosData(tabId){
    		this.$axios.get('http://www.liulongbin.top:3005/api'+ "/getimages/"+ tabId)
    		.then(res=>{
    			this.picList = res.data.message;
    			if(this.picList.length == 0){
    				this.$toast({
    					message:"暂时没有数据",
    					 iconClass: 'iconfont icon-debug'
    				})
    			}
    		})
    		.catch(err=>{
    			console.log(err);
    		})
    	},
      AxiosTab(){
        this.$axios.get('http://www.liulongbin.top:3005/api/getimgcategory')
        .then(res=>{
          this.tabList = res.data.message; 
          this.tabList.unshift({
            title:"全部",
            id:0
          })
        })
        .catch(err=>{
          console.log(err)
        })
    },
    	getTabId(){
    		 eventVue.$on("tabId",(message)=>{
                 this.tabId = message; 
            })
    	}
    }
}
</script>
<style lang="less" scoped>
   .pic-list{
   		padding: 0 12px;
   		width: 100%;
   		li{
   			margin-bottom: 10px;
   			position: relative;
			width: 100%;
   			.tit{
   				position: absolute;
				left:  0;
				bottom: 0;
				font-size: 12px;
				text-align: center;
				height: 30px;
				line-height: 30px;
				color: #fff;
   				width: 100%;
   				background: url(../../../assets/images/watch-bg.png) no-repeat center center/100% 100%;
   			}
   			img{
   				width: 100%;

   			}	
   		}
   } 
</style>


