<template>
	<div class="good-con">
		<div class="swipe">
			<My-Swipe :url="swipeUrl"></My-Swipe>
		</div>
		
		<div class="title">
			{{getdesc.title}}
		</div>
		<div class="price">
			<div class="old-price"><em>市场价:</em><span>{{getdesc.market_price}}</span></div>
			<div class="new-price"><em>促销价:</em><span>{{getdesc.sell_price}}</span></div>
		</div>
		<div class="buy-num">
			<span class="buy">购买数量:</span>     
			<span class="reduce-num btn" @click="reduceNum">-</span><span class="num">{{pickNum}}<transition name="ball" v-on:after-enter="afterEnter"><div class="ball" v-if="isShow">{{pickNum}}</div></transition></span><span class="add-num btn" @click="addNum(getdesc.stock_quantity)">+</span></li><br />
            <mt-button type="primary">立即购买</mt-button>
            <mt-button type="danger" @click="addShopcart">加入购物车</mt-button> 
         
		</div>
      
		<div class="goods-info">
			 	<div class="tit">商品参数</div>
                <div class="name">商品货号：{{getdesc.goods_no || '无'}}</div>
                <div class="stock">库存情况：{{getdesc.stock_quantity}}件</div>
                <div class="tiem">上架时间：{{$moment().format("YYYY-MM-DD HH:MM:SS",getdesc.add_time)}}</div>
		</div>
		<Comment :comAddress="{comAddress:'/getcomments',publish:'/postcomment',comId:$route.query.goodId}"></Comment>
	</div>
</template>
<script>
import {mapActions} from "vuex";
import Comment from "@/components/comment.vue";
	export default {
		data(){
			return{
				getdesc:{},
				swipeUrl:"/getthumimages/"+ this.$route.query.goodId,
				pickNum:0,
				isShow:false
			}
		},
		created(){
			this.axiosGetDesc();
		},
		components:{
			Comment
		},
		methods:{
			...mapActions([
					'getGoodsCart'
			]),
			axiosGetDesc(){
				this.$axios.get('http://www.liulongbin.top:3005/api'+ `/goods/getinfo/${this.$route.query.goodId}`)
				.then(res=>{
					this.getdesc = res.data.message[0]
				})
				.catch(err=>{
					console.log(err);
				})
			},
			afterEnter(){
				this.isShow = false;
			},
			addShopcart(){
				this.isShow = true;
				var obj = {
					num: this.pickNum,
					goodId:this.getdesc.id,
					price:this.getdesc.sell_price,
					title:this.getdesc.title,
					goodNo:this.getdesc.goods_no,
					goodimg:this.$route.query.goodsimg,
					stock:this.getdesc.stock_quantity,
					isSelected:false
				}
				this.getGoodsCart(obj);

			},
			reduceNum(){
				this.pickNum--;
				if(this.pickNum <= 0){
					this.pickNum = 0;
					this.$toast("亲，你不可以给我哦")
				}
			},
			addNum(stock){
				this.pickNum++;
				if(this.pickNum >= stock){
					this.pickNum = stock;
					this.$toast("亲，库存没有那么多了哦")
				}
			}
		}
	}
</script>
<style lang="less" scoped>

/*小球进入的时候 v-if="true" 1秒钟动画*/
.ball-enter-active {
    animation: bounce-in 1s;
}
/*元素移除前默认透明度是1，不让其看见设置为0*/
.ball-leave {
    opacity: 0;
}

@keyframes bounce-in {
    /*translate3d执行更为流畅*/
    0% {
        transform: translate3d(0, 0, 0);
    }
    50% {
        transform: translate3d(140px, -50px, 0);
    }
    75% {
        transform: translate3d(160px, 0px, 0);
    }
    100% {
        transform: translate3d(110px, 300px, 0);
    }
}
	.good-con{
		padding : 0 12px;
		.ball{
			width: 15px;
			height: 15px;
			position: absolute;
			top: 0px;
			left:10px;
			background:red;
			border-radius: 50%;
			font-size: 10px;
			line-height: 14px;
			text-align: center;
			color: #fff;
		} 
		.title{
			font-size: 18px;
			color: blue;
			height: 48px;
			overflow: hidden;
			margin-top: 20px;
			
		}
		.price{
			font-size: 14px;
			.old-price{
				display: inline-block;
				margin-right: 25px; 
				span{
					text-decoration:line-through;
					font-size: 12px;
				}
				em{
					color: #ccc;
					margin-right: 5px;
				}
			}
			.new-price{
				margin-top: 10px;
				display: inline-block;
				color: #f02828;
				span{
					font-size: 16px;
				}
				em{
					margin-right: 5px;
					color: #ccc;
				}
			}
		}
		.buy-num{
			margin-top: 10px;
			.buy{
				font-size: 14px;
				margin-right: 20px;
			}
			.btn{
				display: inline-block;
				width: 25px;
				height: 25px;
				text-align: center;
				line-height: 23px;
				border: 1px solid #ccc;
			}
			.add-num{
				border-left: none;
				border-top-right-radius: 5px; 
				border-bottom-right-radius: 5px; 
			}
			.reduce-num{
				border-right: none;
				border-top-left-radius: 5px; 
				border-bottom-left-radius: 5px; 
			}
			.num{
				display: inline-block;
				width: 25px;
				height: 25px;
				text-align: center;
				line-height: 23px;
				border: 1px solid #ccc;
				position:relative
			}
			.mint-button {
				margin: 10px 0;
				margin-right:20px;
			}
		}
		.goods-info{
			div{
				margin-bottom: 5px;
			}
			.tit{
				text-align: center;
				font-size: 20px;
				font-weight: 700;
				margin-top:  10px;
				margin-bottom:10px; 
			}

		}
	}
</style>


