<template>
	<mt-swipe class="swipe" :auto="4000">
	  <mt-swipe-item v-for="(item,index) in banList" :key="index">
	  	<img :src="item.src||item.img" alt="">
	  </mt-swipe-item>
	</mt-swipe>
</template>
<script>
	export default {
		name:"MySwipe",
		props:["url"],
		data (){
			return {
				banList:null,
			}
		},
		created(){
			this.axiosSwipe();
		},	
		methods:{
			axiosSwipe(){
				this.$axios.get('http://www.liulongbin.top:3005/api' + this.url)
				.then(res=>{
					if(res.status == 200){
						this.banList = res.data.message;
					}
				})
				.catch(err=>{
					console.log(err)
				})
			}
		}

	}
</script>
<style lang="less" scoped> 
	.swipe{
		height:200px;
	}
	/*轮播图样式覆盖*/
	 .mint-swipe img {
	 	height:100%;
	 	width:100%;
	 }
</style>


