const state = {
	userInfo:[],
	isLogin: false 
}
const getters = {
	userInfo: state => state.userInfo,
	isLogin: state => state.isLogin
}

const mutations = {
	userStatus(state,flag){
		state.isLogin = flag;
	},
	setUserInfo(state,info){
		state.userInfo = info;
	}
}

const actions = {
	setUser(content,flag){
		content.commit("userStatus",flag);
	},
	setInfo(content,info){
		content.commit("setUserInfo",info);
	}
}


export default {
	user:true,
	state,
	getters,
	mutations,
	actions
}


