// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

//  init.css
import "@/assets/css/init.css";
//引入字体css
import '@/assets/css/iconfont.css';

//  引入 mint-ui -- 

import MintUl from "mint-ui";
import 'mint-ui/lib/style.css';

Vue.use(MintUl);
//  引入 mint-ui -- 

//  引入 axios -- 
import Axios from 'axios';


//baseURL的处理
// Axios.defaults.baseURL = 'http://www.liulongbin.top:3005/api/';

//挂载
Object.defineProperty(Vue.prototype,'$axios',{
	get(){
		return Axios;
	}
});

//定义拦截器 //开启loading
Axios.interceptors.request.use(config => {
	MintUl.Indicator.open({
	  text: '玩命加载中...',
	  spinnerType: 'triple-bounce'
	});

	return config;
});
//关闭loading
Axios.interceptors.response.use(response =>{
	MintUl.Indicator.close();
	return response;
});




//  引入 axios -- 






//  moment -- 
import Moment from "moment";

Vue.prototype.$moment = Moment;
//  moment -- 


//  引入 js-cookie -- 
import Cookies from 'js-cookie'

Vue.prototype.$Cookies = Cookies;
//  引入 js-cookie -- 


//  全局组件
import MySwipe from "./components/MSwipe.vue"
Vue.component(MySwipe.name,MySwipe);

import NineGirdUl from "./components/NineGirdUl.vue"
Vue.component(NineGirdUl.name,NineGirdUl);

import NineGirdLi from "./components/NineGirdLi.vue"
Vue.component(NineGirdLi.name,NineGirdLi);

import SlideTab from "./components/slideTab.vue"
Vue.component(SlideTab.name,SlideTab);

import Website from "./components/website.vue"
Vue.component(Website.name,Website);

//  全局组件


//  引入 vuex -- 
import store  from '@/store/index.js';
//  引入 vuex -- 


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



