<template>
	<div class="home">
		<My-Swipe url="/getlunbo"></My-Swipe>
		<!-- 下有九宫格 -->
		<nine-gird-ul>
	         <nine-gird-li v-for="(item,index) in grids" :key="index">
	            <router-link :to="item.className">
	               <div :class="'back-img ' + item.className">
	                   {{item.title}}
	               </div>
	            </router-link>
	        </nine-gird-li>
        </nine-gird-ul>
	</div>
</template>
<script>
	export default {
		data(){
			return {
				
				grids:[
					{ 
						route:{name:'news.list'} ,
						title:'新闻列表',
						className:'news'
					},
					{ 
						route:{ 
							name:'photo.list',
						 	params:{
						 		categoryId:0
							}
						},
						title:'图文分享',
						className:'share',
					},
					{   
						route:{
							name:'goods.list',
							query:{
								//默认页码1
								page:1
							}
						} ,
						title:'商品展示',
						className:'goods'
					},
					{ 
						route:{name:'meitu'},
						title:'美图',
						className:'meitu'
					},
					{ 
						route:{name:'satin'},
						title:'娱乐段子',
						className:'satin'
					},
					{   
						route:{name:'noval'},
						title:'书的海洋',
						className:'noval'
					},
				]
			}
		}
	}
</script>
<style lang="less" scoped>



/*九宫格样式*/
.back-img{
        background-size: 50px;
        background-repeat: no-repeat;
        background-position: 50% 15px;
        font-size: 14px;
        color: #000;
        font-family: 'Microsoft YaHei';
        height: 100%;
}


li a{
    text-decoration: none;
    color:black;
}
/*九宫格图片样式*/
.news {
	background-image: url(../../assets/images/news.png);
}
.share {
	background-image: url(../../assets/images/picShare.png);
}
.goods {
	background-image: url(../../assets/images/goodsShow.png);
}
.meitu {
	background-image: url(../../assets/images/feedback.png);
}
.satin {
	background-image: url(../../assets/images/search.png);
}
.noval {
	background-image: url(../../assets/images/callme.png);
}


</style>


