import Vue from "vue";
import Vuex from "vuex";

import shopcart from "./modules/shopcart.js";
import user from "./modules/user.js";




Vue.use(Vuex);


const store = new Vuex.Store({
	modules:{
		shopcart,
		user
	}
});


export default store;


