<template>
        <mt-header :title="$route.meta.title" id="header">
          <div slot="left">
            <mt-button icon="back" @click="go">返回</mt-button>
          </div>
          <mt-button icon="more" slot="right"></mt-button>
        </mt-header>
</template>

<script>
export default {
  name: 'Header',
  props:["title"],
  data () {
    return {
      
    }
  },
  methods:{
    go(){
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="less">
      #header {
        position: fixed;
        top:  0;
        left:  0;
        width: 100%;
        z-index: 999;
      }
</style>



