<template>
	<div>
		<h2>评论专区</h2>
		<div class="publish">
			<textarea v-model="publishCom">
				
			</textarea>
			<button data-v-c38aeb84="" class="mint-button mint-button--primary mint-button--large" @click="publishcomment"> 
				<label class="mint-button-text">发表评论</label>
			</button>
		</div>
		
		<ul class="comment-content">
			<li v-for="(item,index) in comList" :key="index">
				
				<div class="name-time clearfix">
					<div class="name left">{{item.user_name}}</div>
					<div class="time right">
						{{$moment().format("YYYY-MM-DD HH:MM:SS",item.add_time)}}
					</div>
				</div>
				<div class="content">
					{{item.content}}
				</div>
			</li>
		</ul>
		<button data-v-c38aeb84="" class="mint-button mint-button--danger mint-button--large is-plain" @click="moreCom"> <label class="mint-button-text">加载更多</label></button>
	</div>
</template>
<script>
	export default{
		name:'comment',
		props:["comAddress"],
		data(){
			return {
				comList:[],
				page:1,
				publishCom:''
			}
		},
		created(){
			this.axiosComment();
		},
		methods:{
			publishcomment(){
				this.$axios.post('http://www.liulongbin.top:3005/api' + this.comAddress.publish+'/'+this.comAddress.comId,{
					artid:this.comAddress.comId,
					content:this.publishCom
				})
				.then(res=>{
					this.axiosComment();
					this.publishCom ="";
					this.$toast({
	    					message:"提交成功了",
	    					 iconClass: 'iconfont icon-debug'
	    			})
				})
				.catch(err=>{
					console.log(err);
				})
			},
			axiosComment(){
				this.$axios.get('http://www.liulongbin.top:3005/api' + this.comAddress.comAddress+"/"+this.comAddress.comId+'?pageindex='+this.page)
				.then(res=>{
					if(res.data.message.length <= 0){
						this.$toast({
	    					message:"已经没有数据",
	    					 iconClass: 'iconfont icon-debug'
	    				})
	    				return;
					}
					if(this.page == 1){
						this.comList = res.data.message;
					}else{
						this.comList = this.comList.concat(res.data.message);
					}
				})
				.catch(err=>{
					console.log(err);
				})
			},
			moreCom(){
				this.page++;
				this.axiosComment();
			}
		}
	}
</script>
<style scoped lang="less">
	h2{
		padding: 10px 0;
		border-bottom: 1px solid #ccc;
		border-top:  1px solid #ccc;
		text-align: center;
		margin: 20px 0; 
	}
	.publish{
		margin-bottom: 10px;
		textarea{
			resize:none;
			width: 100%;
			height: 100px;
			margin-bottom: 10px;
			padding: 10px;  
		}
	}
	.comment-content{
		padding-bottom: 20px;
		margin-top: 30px;
		li{
			border-bottom: 1px solid #ccc;
			padding-bottom: 10px;
			.name-time {
				margin: 10px 0;
				font-size: 12px;
				font-weight: 700;
	
			}
			.content{
				font-size: 16px;
				color: #605e5e;

			}
		}	
	}
</style>


