<template>
	<div class="news" id="news">
		<ul>
			<li v-for="(item,index) in newsList" :key="index">
				<div class="news-img">
					<img :src="item.img_url" alt="">
				</div>
				<div class="news-tit">
					{{item.title}}
				</div>
				<div class="news-desc">
					{{item.zhaiyao}}
				</div>
				<div class="news-info clearfix">
					<div class="left news-time">
						{{$moment().format('MM-DD HH:MM:SS', item.add_time)}}
					</div>
					<div class="right news-click">
						<i :class="item.click == 0? '' : 'act'"></i>{{ item.click }}
					</div>
				</div>
				<router-link :to="{'name':'newsDetails',query:{newsId:item.id}}"></router-link>
			</li>
		</ul>
	</div>
</template>
<script>
	export default {
		data(){
			return {
				newsList: []
			}
		},
		created(){
			this.$axios.get('http://www.liulongbin.top:3005/api' + "/getnewslist")
			.then(res=>{
				this.newsList = res.data.message;
			})
			.catch(err=>{
				console.log(err);
			})

		}
	}
</script>
<style lang="less" scoped >
	.news{
		ul{
			padding:  5px 10px;
			li{
				position: relative;
				padding-left: 155px;
				margin-bottom: 8px;
				border-bottom:  1px solid #ccc;
				a{
					position: absolute;
					width: 100%;
					height: 100%;
					top: 0;
					left: 0;
				}
				.news-img{
					position: absolute;
					top:  0;
					left:  0;
					width: 150px;
					height:  100px;
					img{
						width: 100%;
						height: 100%;
					}
				}
				.news-tit{
					font-size: 14px;
					height: 38px;
					overflow:hidden;
				}
				.news-desc{
					font-size: 12px;
					color: #c0c0c0;
					margin: 6px 0;
					height: 32px;
					overflow:hidden;
				}
				.news-info{
					font-size: 12px;
					height: 25px;
    				line-height: 25px;
				}
				.news-time{
					color:#ccc; 
				}
				.news-click{
					color: #716f6f;
					i{
						display: inline-block;
						width: 20px;
						height: 20px;
						vertical-align: top;
						margin-right: 10px;
						background: url(../../../assets/images/ico_comm.png) no-repeat 0 0 ;
					}
					i.act{
						background: url(../../../assets/images/ico_comm.png) no-repeat -25px 0 ;
					}
				}
			}
		}
	}
</style>


