<template>
	<mt-tabbar v-model="selected" id="footer">
		  <mt-tab-item id="home">
		    <img slot="icon" src="@/assets/images/index.png">
		    首页
		  </mt-tab-item>
		  <mt-tab-item id="search">
		    <img slot="icon" src="@/assets/images/find.png">
		    查找
		  </mt-tab-item>  
		  <mt-tab-item id="shopcart">
		    <img slot="icon" src="@/assets/images/shopcart.png">
		    购物车<mt-badge size="small" type="error">{{getTotal}}</mt-badge>
		  </mt-tab-item>
		  <mt-tab-item id="personal">
		    <img slot="icon" src="@/assets/images/vip.png">
		    个人中心
		  </mt-tab-item>
	</mt-tabbar>
</template>

<script>
import {mapGetters} from "vuex";
export default {
  name: 'Footer',
  data () {
	    return {
	      selected:''
	    }
    },
   computed:{
		...mapGetters({
			getTotal:'getTotalCount'
		})
   },
  	watch:{
		selected(newVal){
			this.$router.push({
					name:newVal
				});
		}
  }
}
</script>

<style scoped lang="less">
	#footer{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		z-index: 999;
	}
</style>



