<template>
	<div class="nav nav-deep">
		<div class="nav-bg"></div>
		<div class="nav-list">
			<ul class="clearfix">
				<li v-for="(item,index) in tabList" :key="index" >
					{{ item.title }}
					<a href="javaScript:;" class="cover" @click="setTabId(item.id)"></a>
				</li>
			</ul>
		</div>
		<div class="nav-shade-left"></div>
		<div class="nav-shade-right"></div>
	</div>
</template>
<script>
	import eventVue from "@/assets/js/eventVue.js";

	export default {
		name:"SlideTab",
		props:["tabList"],
		methods:{
			setTabId(id){
				eventVue.$emit("tabId",id);
			}
		}

	}
</script>
<style lang="less" scoped >
	.nav {
	    position: relative;
	    width: 100%;
	    height: 40px;
	    background-color: #fff;
	    .nav-bg {
		    background-color: #2e2e2e;
		    border-bottom: 1px solid #dcdcdc;
		}
		.nav-list {
		    position: absolute;
		    top: 0;
		    left: 0;
		    width: 100%;
		    height: 40px;
		    overflow-x: scroll;
		    overflow-y: hidden;
		    ul {
			    position: relative;
			    white-space: nowrap;
			    height: 40px;
			    z-index: 0;
			    li {
				    display: inline-block;
				    height: 40px;
					padding: 0 10px;
				    line-height: 40px;
				    text-align: center;
				    position:relative;
				 	a {
					    color: #ffffff; 
					    font-size: 15px;
					    color: #000;
					}
				}
			}
		}
		.nav-shade-left {
		    position: absolute;
		    left: 0;
		    top: 0;
		    width: 30px;
		    height: 40px;
		    background: -webkit-linear-gradient(left, #2e2e2e 0%, rgba(0,0,0,0) 100%);
		    background: -o-linear-gradient(left, #2e2e2e 0%, rgba(0,0,0,0) 100%);
		    background: linear-gradient(to right, #2e2e2e 0%, rgba(0,0,0,0) 100%);
		}
		.nav-shade-left {
		    position: absolute;
		    left: 0;
		    top: 0;
		    width: 30px;
		    height: 40px;
		    background: -webkit-linear-gradient(left, #fff 0%, rgba(255,255,255,0) 100%);
		    background: -o-linear-gradient(left, #fff 0%, rgba(255,255,255,0) 100%);
		    background: linear-gradient(to right, #2e2e2e 0%, rgba(0,0,0,0) 100%);
		    background: linear-gradient(left, #fff 0%, rgba(255,255,255,0) 100%);
		}
		.nav-shade-right {
		    position: absolute;
		    right: 0;
		    top: 0;
		    width: 30px;
		    height: 40px;
		   background: -webkit-linear-gradient(left, #2e2e2e 0%, rgba(0,0,0,0) 100%);
		   background: -o-linear-gradient(left, #2e2e2e 0%, rgba(0,0,0,0) 100%);
		   background: linear-gradient(to right, #2e2e2e 0%, rgba(0,0,0,0) 100%);
		}
		.nav-shade-right {
		    position: absolute;
		    right: 0;
		    top: 0;
		    width: 30px;
		    height: 40px;
		    background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, #fff 100%);
		    background: -o-linear-gradient(left, rgba(255,255,255,0) 0%, #fff 100%);
		    background: linear-gradient(to right, #2e2e2e 0%, rgba(0,0,0,0) 100%);
		    background: linear-gradient(left, rgba(255,255,255,0) 0%, #fff 100%);
		}
	}
</style>


