<template>

 <div class="main-body" ref="wrapper" :style="{ height: (wrapperHeight-50) + 'px' }">
      <mt-loadmore  :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :autoFill="isAutoFill">
		<ul class="goods-list clearfix">
			<li v-for="(item,index) in goodsList" :key="index">
				<div class="goods-img">
					 <img  :src="item.img_url" alt="">
				</div>
				<div class="goods-tit">
					{{item.title}}
				</div>
				<div class="goods-desc">
					{{item.zhaiyao}}
				</div>
				<div class="goods-price">
					<div class="market">
						<em>价格:</em><span>{{item.market_price}}</span>
					</div>
					<div class="sell clearfix">
						<div class="left">
							<em>促销价:</em><span>{{item.sell_price}}</span>
						</div>
						<div class="right">
							<em>库存:</em><span>{{item.stock_quantity}}</span>
						</div>
					</div>
				</div>
				<div class='goods-info clearfix'>
					<div class="time left">{{$moment().format("YYYY-MM-DD",item.add_time)}}</div>
					<div class="click right"><i :class="item.click == 0? '' : 'act'"></i>{{item.click}}</div>
				</div>
				<router-link :to="{'name':'goodsDetails',query:{goodId:item.id,goodsimg:item.img_url}}" class="cover"></router-link>	
			</li>
		</ul>
		  </mt-loadmore>
    </div>
</template>
<script>
	export default {
		data(){
			return{
				page:1,
				goodsList:[],
				 //可以进行上拉
			      allLoaded: false,
			      //是否自动触发上拉函数
			      isAutoFill: false,
			      wrapperHeight: 0,
			}
		},
		created(){
			this.axiosGoods();
		},
		  mounted() {
		    // 父控件要加上高度，否则会出现上拉不动的情况
		    this.wrapperHeight =
		      document.documentElement.clientHeight -
		      this.$refs.wrapper.getBoundingClientRect().top;
		   
		  },
		methods:{
			    // 上拉加载
		    loadBottom() {
		      this.loadMore();
		    },
			axiosGoods(){
				this.$axios.get('http://www.liulongbin.top:3005/api' + `/getgoods?pageindex=${this.page}`)
				.then(res=>{
					this.goodsList = res.data.message;
					console.log(this.goodsList );
					this.page++;
				})
				.catch(err=>{
					console.log(err)
				})
			},
			   // 加载更多
    loadMore() {
      this.$axios.get('http://www.liulongbin.top:3005/api' + `/getgoods?pageindex=${this.page}`)
        .then(res => {
          // concat数组的追加
          console.log(res);
          this.goodsList = this.goodsList.concat(res.data.message);
          if (res.data.message.length <= 0) {
          	this.$toast("没有数据了，亲")
            this.allLoaded = true; // 若数据已全部获取完毕
          }
          this.page++;
          this.$refs.loadmore.onBottomLoaded();
        })
        .catch(error => {
          console.log(error);
          alert("网络错误，不能访问");
        });
    }
		}
	}
</script>
<style lang="less" scoped>
.main-body {
  /* 加上这个才会有当数据充满整个屏幕，可以进行上拉加载更多的操作 */
  overflow: scroll;
}
	.goods-list{
		padding: 10px 12px; 
		margin-left:  -10px;
		li{
			position:relative;
			width: 47%;
			float: left;
			border: 1px solid #ccc;
			margin-left: 9px; 
			margin-bottom: 10px;
			padding: 5px 5px;
			border-radius: 5px;
			overflow:hidden;
			.goods-img{
			    width: 100%;
	    		height: 180px;
	    		margin: 0 auto;
				img{
					width: 100%;
					height: 100%;
				}
			}
			.goods-tit{
				margin-top: 3px; 
				height: 38px;
				overflow: hidden;
				font-size: 14px;
				font-weight: 700;
			}
			.goods-desc{
				margin-top: 3px; 
				height: 32px;
				overflow: hidden;
				font-size: 12px;
				color: #6c6a6a;
			}
			.goods-price{
				margin-top: 3px; 
				.market{
					color: #000;
					font-size: 12px;
					em{
						color: #a7a7a7;
						margin-right: 3px; 
					}
					span{
							text-decoration:line-through;
					}
				}
				.sell{
					color: #f02828;
					font-size: 12px;
					font-weight:700;
					em{
						margin-right: 3px; 
						font-weight:300;
						color: #a7a7a7;
					}
				}
			}
			.goods-info {
				margin-top: 3px;
				font-size: 12px;
				.click{
					i{
						display: inline-block;
					    width: 14px;
    					height: 14px;
						vertical-align: top;
						margin-right: 5px;
						background-image: url(../../../assets/images/ico_comm.png);
						background-repeat: no-repeat;
						background-position: 0 0;
						background-size: 30px;
					}
					i.act{
						background-position: -16px 0;
					}
				}
			}
		}
	}
</style>


