import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/index'
// HOME
import Home from "@/view/home/home"
//  HOME 新闻页面
import News from "@/view/home/news/news"
import NewsDetails from "@/view/home/news/newsDetails"
//  HOME  图片分享页面
import Share from "@/view/home/share/share"
import ShareDetails from "@/view/home/share/shareDetails"
// HOME 商品页面
import Goods from "@/view/home/goods/goods"
import GoodsDetails from "@/view/home/goods/goodsDetails"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
		component: Index,
		meta:{
			title:"框架",
			index:0
		},
      children:[
	      {
	      	path:'/',
	      	name:'home',
			component:Home,
			meta:{
				title:'首页',
				index:1
			}
	      },
	      {
	      	path:"/news",
	      	name:'news',
			component:News,
			meta:{
				title:'新闻',
				index:2
			}
	      },
	      {
	      	path:"/news/newsDetails/:newsId",
	      	name:'newsDetails',
			component:NewsDetails,
			meta:{
				title:"新闻详情",
				index:3
			}
		   },
		  {
			path:'/share',
			name:'share',
			component:Share,
			meta:{
				index:2,
				title:'图片分享'
			}
		  },
		  {
		  	path:"/share/shareDetails/:picId",
		  	name:"shareDetails",
		  	component:ShareDetails,
		  	meta:{
				index:3,
				title:'图片分享详情'
			}
		  },
		  {
		  	path:'/goods',
		  	name:"goods",
		  	component:Goods,
		  	meta:{
		  		index:2,
		  		title:"商品页面"
		  	}
		  },
		   {
		  	path:'/goodsDetails/:goodId',
		  	name:"goodsDetails",
		  	component:GoodsDetails,
		  	meta:{
		  		index:3,
		  		title:"商品详情页面"
		  	}
		  },
		  {
		  	path:'/noval',
		  	name:'noval',
		  	component:()=>import("@/view/home/noval/noval"),
		  	meta:{
		  		index:2,
		  		title:"小说"
		  	}
		  },
		    {
		  	path:'/novalDetails',
		  	name:'novalDetails',
		  	component:()=>import("@/view/home/noval/novalDetails"),
		  	meta:{
		  		index:3,
		  		title:"小说详情"
		  	}
		  },
		    {
		  	path:'/novalSearch',
		  	name:'novalSearch',
		  	component:()=>import("@/view/home/noval/novalSearch"),
		  	meta:{
		  		index:3,
		  		title:"小说搜索"
		  	}
		  },
		  {
		  	path:"/meitu",
		  	name:'meitu',
		  	component:() => import("@/view/home/meitu/meitu"),
		  	meta:{
		  		index:2,
		  		title:"美女"
		  	}
		  },
		  {
		  	path:"/satin",
		  	name:'satin',
		  	component:()=> import("@/view/home/satin/satin"),
		  	meta:{
		  		index:2,
		  		title:'段子'
		  	}
		  },
		  {
		  	path:"/shopcart",
		  	name:'shopcart',
		  	component:()=> import("@/view/shopcart/shopcart"),
		  	meta:{
		  		index:1,
		  		title:"购物车"
		  	}
		  },
		  {
		  	path:"/personal",
		  	name:'personal',
		  	component:()=> import("@/view/personal/personal"),
		  	meta:{
		  		index:1,
		  		title:"个人中心"
		  	}
		  },
		  {
		  	path:"/login",
		  	name:'login',
		  	component:() => import("@/view/personal/login/login"),
		  	meta:{
		  		index:2,
		  		title:"登录"
		  	}
		  },
		  {
		  	path:'/register',
		  	name:'register',
		  	component:()=> import("@/view/personal/register/register"),
		  	meta:{
		  		index:2,
		  		title:"注册"
		  	}
		  },
		  {
		  	path:"/search",
		  	name:'search',
		  	component:() => import("@/view/search/search"),
		  	meta:{
		  		index:1,
		  		title:"搜索"
		  	}
		  },
		   

      ]
    }
  ]
})



