<template>
	<div class="pic-info">
		<nine-gird-ul class="clearfix">
			<nine-gird-li v-for="(item,index) in imgs" :key="index">
				<img class="share-img" :src="item.src" alt="">
			</nine-gird-li>
		</nine-gird-ul>
		<div class="img-info">
			 <div class="tit">
			 	 {{info.title}}
			 </div>
			 <div class="clearfix">
			 	<div class="left time">
			 		{{$moment().format('YYYY-MM-DD HH:MM:SS', info.add_time)}}
			 	</div>
			 	<div class="right click">
			 		<i :class="info.click == 0? '' : 'act'"></i>
			 		{{ info.click }}
			 	</div>
			 </div>
			 <div class="content" v-html="info.content">
			 </div>
		</div>
		<Comment :comAddress="{comAddress:'/getcomments',publish:'/postcomment',comId:$route.query.picId}"></Comment>
	</div>
</template>
<script>
import Comment from "@/components/comment.vue";
	export default {
		data(){
			return{
				imgs:[],
				info:{}
			}
		},
		components:{
			Comment
		},
		created(){
			this.axiosImages();
			this.axiosInfo();
		},
		methods:{
			axiosImages(){
				this.$axios.get( 'http://www.liulongbin.top:3005/api'+"/getthumimages/" + this.$route.query.picId)
				.then(res=>{
					this.imgs = res.data.message;
				})
				.catch(err=>{
					console.log(err);
				})
			},
			axiosInfo(){
				this.$axios.get('http://www.liulongbin.top:3005/api'+"/getimageInfo/" + this.$route.query.picId)
				.then(res=>{
					this.info = res.data.message[0];
				})
				.catch(err=>{
					console.log(err);
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.pic-info{
		padding: 10px 12px;
		.share-img{
			width: 100%;
			height: 100%;
		}
		.img-info{
			padding-bottom: 10px;
			.tit{
				font-size:18px;
				font-weight: 700;
				margin-bottom: 10px;
			}
			.clearfix{
				padding:0 5px 10px;
				border-bottom: 1px solid #ccc;
				color: #ccc;
				margin-bottom: 10px; 
				i{
					display: inline-block;
					width: 20px;
					height: 20px;
					vertical-align: top;
					margin-right: 10px;
					background: url(../../../assets/images/ico_comm.png) no-repeat 0 0 ;
				}
				i.act{
					background: url(../../../assets/images/ico_comm.png) no-repeat -25px 0 ;
				}
			}
		}
	}
</style>


