const state = {
	goodsCart:JSON.parse(window.localStorage.getItem('goodList')||'[]'),
}

const getters = {
	newGoodsCart(state){
		return state.goodsCart;
	},
	getTotalCount(state){
		let num = 0;
		state.goodsCart.forEach(el => {
			num += el.num
		});
		return num;
	},
	priceTotal(state){

		let price = 0;
		state.goodsCart.forEach(el => {
			if(el.isSelected){
				price += el.num *  el.price;
			}
		});
		return price;
	},
	isSwitch(state){
		let isShow = false;
		let isSeNum = 0;
		for(var i = 0; i<= state.goodsCart.length-1; i++){
			if(state.goodsCart[i].isSelected == true){
				isSeNum += 1;
			}
		}
		if(isSeNum == state.goodsCart.length){
			isShow = true;
		}else{
			isShow = false;
		}
		return isShow;
	}

}

const mutations = {
	pushGoodsCart(state,goods){
		if(typeof goods != 'object') return;
		if(state.goodsCart.length == 0 ){
			state.goodsCart.unshift(goods);
			//  locaStorage
			window.localStorage.setItem("goodList",JSON.stringify(state.goodsCart));
		}else{
			let lock = true;
			for(var i = 0; i<= state.goodsCart.length-1; i++){
				if(goods.goodId == state.goodsCart[i].goodId){
					state.goodsCart[i].num += goods.num;
					lock = false;
				}
			}
			if(lock){
				state.goodsCart.push(goods)
			}

		//  locaStorage
		window.localStorage.setItem("goodList",JSON.stringify(state.goodsCart));
		}
	},
	deleteGoodsCart(state,id){
		state.goodsCart.splice(id,1);
		window.localStorage.setItem("goodList",JSON.stringify(state.goodsCart));
	},	
	storageSetItem(obj){
		window.localStorage.setItem("goodList",JSON.stringify(obj));
	}
}

const actions = {
	getGoodsCart(content,goods){
		content.commit("pushGoodsCart",goods)
	},
	delGoodsCart(content,id){
		content.commit("deleteGoodsCart",id)
	},
	SetItem(content,obj){
		content.commit("storageSetItem",obj)
	}
}


export default {
	shopcart:true,
	state,
	getters,
	mutations,
	actions
}


