<template>
	<div class="news-details">
		<div class="title">
			{{newsdetail.title}}
		</div>
		<div class="info clearfix">
			<div class="left time">
				{{ $moment().format('YYYY-MM-DD HH:MM:ss',newsdetail.time)  }}
			</div>
			<div class="right click">
				<i :class="newsdetail.click == 0? '' : 'act'"></i>{{newsdetail.click}}
			</div>
		</div>
		<div class="content" v-html="newsdetail.content">

		</div>	
	</div>
</template>
<script>
	export default {
		name:"newsDetails",
		data(){
			return{
				newsdetail:{}
			}
		},
		created(){
			console.log(this.$route.query.newsId);
			this.$axios('http://www.liulongbin.top:3005/api'+"/getnew/"+ this.$route.query.newsId)
			.then(res=>{
				this.newsdetail = res.data.message[0];
			})
			.catch(err=>{
				console.log(err);
			})
		}
	}
</script>
<style lang="less" scoped>
	.news-details{
		padding: 0 10px 10px;
		.title{
			padding: 5px 0;
			font-size: 20px;
			color: #000;

		}
		.info{
			color: #ccc;
			font-size: 14px;
			padding-bottom: 10px;
			margin-bottom: 10px;

			border-bottom: 1px solid #ccc; 
			i{
				display: inline-block;
				width: 20px;
				height: 20px;
				vertical-align: top;
				margin-right: 10px;
				background: url(../../../assets/images/ico_comm.png) no-repeat 0 0 ;
			}
			i.act{
				background: url(../../../assets/images/ico_comm.png) no-repeat -25px 0 ;
			}
		}
	}
</style>


